.App {
  text-align: center;
}

#container-h__matrix-path-value{
  display: flex;
}

table{
  display: inline-table;
}

table label{
  text-align: right;
}

.input-path-value{
  width: 2em;
  margin: 2px;
}

#container__input-matrix, #x{
  display: flex;
}

#container__input-matrix > *, #x > *{
  width: 50%;
}

#container__input-matrix-file, #container__input-size-matix{
  border-right: 1px solid black;
}

.start{
  background-color: rgba(255, 0, 0, 0.5);
}

.end{
  background-color: rgba(0, 0, 255, 0.5);
}

.path{
  background-color: rgba(0, 255, 0, 0.5);
}
